.head-section {
  width: 100%;
  height: 19.25vw;
  border-top: 14px solid #d8cdba;
  background: url('../images/sign-in-bg-d.jpg') no-repeat center;
  background-size: cover;
}

.sign-in-and-sign-up {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 450px;
  max-width: 450px;
  box-shadow: 0 1.5px 25px 0 rgba(88, 80, 119, 0.19);
  border-radius: 5px;
}

.logo2 {
  width: 168px;
}

a {
  display: block;
  color: #585077;

  &:hover,
  &:focus {
    color: #585077;
  }
}

// Tabs
.tabs {
  bottom: -4px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  background-image: linear-gradient(#fff, #f1f0f4);

  .divider {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 2px;
    height: 46px;
    background-color: #585077;
  }

  .tab {
    width: 49%;
    line-height: 1.5;
    padding: 26px;
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    color: #b3b3b3;
    text-align: center;

    &.active {
      position: relative;
      color: #585077;
      font-weight: 900;

      &:after {
        position: absolute;
        bottom: 4px;
        left: 50%;
        transform: translate(-50%, 0);
        display: block;
        content: '';
        width: calc(100% - 64px);
        height: 4px;
        background-color: #585077;
      }
    }
  }
}

.custom-checkbox {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 0;
}

.custom-control-label {
  font-size: 1.25rem;

  &:before {
    width: 32px;
    height: 32px;
    border-radius: 2px;
    border: .5px solid #d6d6d6;
    background-color: #ffffff;
  }
}

.custom-control-input:checked ~ .custom-control-label:before {
  border-color: #d6d6d6;
  background-color: #ffffff;
}

.custom-control-input:checked ~ .custom-control-label:after {
  left: -16px;
  width: 24px;
  height: 24px;
  border-radius: 1px;
  background-color: #585077;
  background-image: none;
}

.terms-and-conditions {
  &:after {
    display: block;
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: #585077;
  }
}

.text-block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 32px 0;
  font-family: "Raleway", sans-serif;
  color: #585077;
}

.submit-button {
  background-color: #585077;
  border-radius: 5px;
  z-index: 1;

  .btn {
    top: -6px;
    width: 100%;
    height: 74px;
    padding-top: 14px;
    background-color: #585077;
    border-radius: 5px;
    border: none;

    &:active {
      background-color: #585077 !important;
      box-shadow: none !important;
    }

    &:focus {
      box-shadow: none !important;
    }

    .fa-long-arrow-right {
      position: absolute;
      right: 32px;
      top: 38%;
      font-size: 2.25rem;
    }
  }
}

.options {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
}

.sign-in,
.sign-up {
  z-index: 2;
  min-height: 420px;
  padding: 0 30px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 1.5px 25px 0 rgba(88, 80, 119, 0.19);
  font-family: "Raleway", sans-serif;
  color: #585077;
}

// Form
.input-group-select {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.input-label {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  white-space: nowrap;
}

.input-group {
  height: 68px;
  border-radius: 2.5px;
  border: 1px solid #ced4da;
  background-color: #fff;
}
.custom-select {
  height: 100%;
}

.form-control {
  height: 100%;
  color: #585077;
  border-left: none;
  font-size: 1.25rem;

  &:focus {
    border: 1px solid #ced4da;
    border-radius: 2.5px;
    border-left: 0;
  }

  &::-webkit-input-placeholder {
    height: 100%;
    color: #585077;
    font-size: 1.25rem;
  }
  &::-moz-placeholder {
    height: 100%;
    color: #585077;
    font-size: 1.25rem;
  }
  &:-ms-input-placeholder {
    height: 100%;
    color: #585077;
    font-size: 1.25rem;
  }
  &:-moz-placeholder {
    height: 100%;
    color: #585077;
    font-size: 1.25rem;
  }
}

.input-group-text {
  height: 100%;
  color: #585077;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  background-color: transparent;
  border-right: none;

  .input-group-aiqoo-text {
    min-width: initial;
  }

  .far {
    font-size: 1.5rem;
  }

  &:after {
    display: block;
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    width: 2px;
    height: 65%;
    background-color: #dedede;
  }
}

.input-group-password {
  .fa-eye {
    z-index: 3;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    font-size: 1.5rem;
    color: #585077;
  }

  .form-control {
    padding-right: 50px;
  }
}

.custom-checkbox {
  display: flex;
