}

.forgot-password {
  margin-left: auto;
  font-size: 1.25rem;
}

.custom-control {
  display: flex;
  flex-direction: row;
  align-items: center;

  .custom-control-label {
    padding-top: .025em;

    &:before { top: 0; }
  }

  .custom-control-input:checked ~ .custom-control-label::before {
    background-color: transparent !important;
  }

  .custom-control-input:checked ~ .custom-control-label::after {
    background-image: url("../images/confirm-7.svg");
    //top: 0;
  }
}

// Checkbox
.custom-control-label {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
  font-size: 1.25rem;

  &::before,
  &::after {
    display: none;
  }
}

.custom-checkbox-ui {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.5rem;
  height: 1.5rem;
  background-color: white;
  border: 1px solid #585077;
  border-radius: 5px;
}

.custom-control-input:checked ~ .custom-checkbox-ui::before {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  background: url(../images/confirm-7.svg) no-repeat center;
  background-size: 50% auto;
}

.input-label {
  padding-left: 10px;
}

